.contact {
    color: #222930;
    overflow-x: hidden;
    height: 100vh;
}

.hide {
    display: none;
}

.info {
    color: #4EB1BA
}

.info:hover + .hide {
    display: block;
    color: #E9E9E9;
}

.card {
    padding: 30px 40px;
    margin-bottom: 60px;
    border: none !important;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.2);
    background-color: #222930
    ;
}

.blue-text {
    color: #E9E9E9
}

.form-control-label {
    margin-bottom: 0;
    color: #E9E9E9
}

input,
textarea,
button, select {
    padding: 8px 15px;
    border-radius: 5px !important;
    margin: 5px 0px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    font-size: 18px !important;
    font-weight: 300
}

input:focus,
textarea:focus {
    -moz-box-shadow: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border: 1px solid #00BCD4;
    outline-width: 0;
    font-weight: 400
}

.btn-block {
    text-transform: uppercase;
    font-size: 15px !important;
    font-weight: 400;
    height: 43px;
    cursor: pointer
}

.btn-block:hover {
    color: #fff !important
}

button:focus {
    -moz-box-shadow: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    outline-width: 0
}