.afbeeldingcontainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.afbeeldingcontainer .afbeelding {
    position: relative;
    width: 280px;
    height: 400px;
    background: #222930;
    transition: 0.5s;
    overflow: hidden;
}

.afbeeldingcontainer .afbeelding figure {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.afbeeldingcontainer .afbeelding:before {
    content: '';
    position: absolute;
    top: -100%;
    left: 0;
    width: 50%;
    height: 100%;
    background: #222930;
    z-index: 1;
    transition: 0.5s;
}

.afbeeldingcontainer .afbeelding:after {
    content: '';
	position: absolute;
	bottom: -100%;
	right: 0;
	width: 50%;
	height: 100%;
	background: #222930;
	z-index: 1;
	transition: 0.5s;
}

.afbeeldingcontainer:hover .afbeelding:before {
    top: 0;
}

.afbeeldingcontainer:hover .afbeelding:after {
    bottom: 0;
    transition-delay: 0.5s;
}

.afbeeldingcontainer .afbeelding:hover:before {
    top: -100%;
}

.afbeeldingcontainer .afbeelding:hover:after {
    bottom: -100%;
}